.container {
  display: grid;
  grid-template-columns: repeat(4, auto);
  max-width: 944px;
  min-height: 856px;
  margin: 50px auto;
  justify-items: space-between;
}

.product-card {
  box-sizing: border-box;
  padding: 30px 14px 14px;
  width: 200px;
  height: 403px;
  border: 1px #f3f3f3 solid;
  border-radius: 5px;
  margin: 0;
  margin-bottom: 50px;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  color: #000;

  &__image {
    display: block;
    margin: 0 auto;
    width: 160px;
    height: 134px;
  }

  &__name {
    margin-top: 45px;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
  }

  &__code {
    margin-top: 5px;
  }

  .price-stroke {
    display: grid;
    grid-template-columns: 1fr auto;
    justify-items: space-between;
    align-items: baseline;
    margin-top: 20px;

    .price-stroke__price-number {
      width: 79px;
      font-weight: bold;
      font-style: normal;
      font-size: 16px;
      line-height: 19px;
    }
  }

  .product-card__button {
    display: grid;
    box-sizing: border-box;
    background-color: #00acdc;
    height: 40px;
    width: 170px;
    border-radius: 5px;
    margin-top: 20px;
    justify-items: center;
    align-items: center;
    color: #fff;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    cursor: pointer;
    text-decoration: none;

    .product-card__link {
      color: #fff;
      text-decoration: none;
    }
  }

  .product-card__button:hover {
    background-color: #fff;
    color: #00acdc;
    border: 1px solid #00acdc;

    .product-card__link {
      color: #00acdc;
    }
  }
}

.rate {
  display: grid;
  grid-template-columns: 1fr auto;
  // justify-items: space-between;
  margin-top: 15px;

  &__stars {
    display: grid;
    grid-template-columns: repeat(5, 15px);
    // justify-items: ;
    grid-gap: 4px;

    .rate__star {
      background: url("../images/star-active.svg") center no-repeat;
      height: 15px;
      width: 15px;
      // margin-right: 4px;
    }

    .rate__star:last-child {
      background: url("../images/star.svg") center no-repeat;
    }
  }

  &__response {
    margin-top: 1px;
  }
}
